body {
  margin: 0;
  padding: 0;
  text-align: center;
  font-family: 'Open Sans', sans-serif;
  color: #3A6355;
  background: #2d2b30; }

/* Link Styles */
a {
  text-decoration: none;
  color: #999; }

a:hover {
  color: #999; }

.clearfix {
  clear: both; }

/* Section Styles */
.main-nav {
  width: 100%;
  background: rgba(45, 43, 48, 0.8);
  min-height: 30px;
  padding: 10px;
  position: fixed;
  text-align: center; }

.nav {
  display: flex;
  justify-content: space-around;
  font-weight: 700;
  list-style-type: none;
  margin: 0 auto;
  padding: 0; }

.nav .name {
  display: block;
  margin-right: auto;
  color: white;
  letter-spacing: 5px; }

.nav li {
  padding: 5px 10px 10px 10px; }

.nav a {
  transition: all .5s; }

.nav a:hover {
  color: white; }

.card {
  margin: 30px;
  padding: 20px 40px 40px;
  text-align: left;
  border-top: 4px solid #781125;
  clear: both; }

.card:hover {
  border-color: #96731A; }

.photo {
  display: block;
  width: 200px;
  height: 0%;
  margin: 0 auto; }

.information {
  width: 100%;
  text-align: center;
  /*display: inline-block;
  position: absolute;*/ }

.heading {
  font-size: 2em;
  text-transform: uppercase;
  color: #781125;
  margin-top: 10px; }

@media screen and (min-width: 840px) {
  .flex {
    display: flex;
    max-width: 1200px; } }

@media screen and (min-width: 840px) {
  main {
    padding-top: 20px; }
  main p {
    line-height: 1.6em; }
  .card {
    margin: 40px auto;
    padding: 20px 40px 40px;
    width: 80%;
    max-width: 1000px; }
  .photo {
    width: 30%;
    margin: 20px 30px; }
  .information {
    text-align: left; } }

header {
  text-align: center;
  background: url("../img/concert02_with_text.jpg") no-repeat top center;
  background-size: cover;
  overflow: hidden;
  padding-top: 60px;
  height: 350px; }

.tag {
  color: black;
  padding: 10px;
  letter-spacing: 5px; }

.name {
  font-weight: 300; }

header .name {
  font-size: 3em; }

.tagline {
  font-weight: 600;
  padding: 20px;
  text-transform: uppercase;
  color: #781125; }

@media screen and (min-width: 840px) {
  header {
    min-height: 470px; }
  .nav {
    max-width: 1200px;
    padding: 0 30px; } }

/* Footer styles */
footer {
  width: 100%;
  min-height: 30px;
  padding: 20px 0 40px 20px; }

footer .copyright {
  top: -8px;
  font-size: .75em; }

footer ul {
  list-style-type: none;
  margin: 0;
  padding: 0; }

footer ul li {
  display: inline-block; }

a.social {
  display: inline-block;
  margin-left: 5px;
  width: 30px;
  height: 30px;
  background-size: 30px 30px;
  opacity: .4;
  transition: all .5s; }

a.twitter {
  background-image: url(../img/twitter-32.png); }

a.youtube {
  background-image: url(../img/youtube-32.png); }

a.spotify {
  background-image: url(../img/spotify-32.png); }

a.social:hover {
  opacity: 1; }

@media screen and (min-width: 840px) {
  footer {
    font-size: 1.3em;
    max-width: 1200px;
    margin: 40px auto; } }

/* Survey page styles */
.photo {
  width: 60%;
  padding: 10px; }

form {
  margin: 0 auto;
  text-align: left;
  display: inline-block; }

.container {
  text-align: center; }

.newsletter {
  text-align: right; }

/* Styles for larger screens */
@media screen and (min-width: 840px) {
  .photo {
    width: 40%; } }

/* Tour page styles */
pre {
  font-family: 'Open Sans', sans-serif; }

.heading {
  display: inline-block;
  margin: 0; }

.pick {
  width: 40px;
  height: 0%;
  padding: 10px; }
